const setToken = async () => {
  const adminArray = await fetch(
    'https://641201246e3ca3175304119e.mockapi.io/api/auth/admin'
  );
  const array = await adminArray.json();
  return await array[0].tg_tok;
};

const telegramOptions = {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json;',
  },
};

export const sendOrderToTelegram = async ({
  customerName,
  customerPhone,
  routeBeginCity,
  routeEndCity,
  routeBeginDate,
  customerComment,
  driverTelegramID,
  serviceType,
}) => {
  const heading =
    serviceType === 'parcel'
      ? `   :`
      : `   :`;
  const TOKEN = await setToken();
  // const TEXT = `${heading}
  // ${'__'}A I': ${customerName}${'__'}
  // ${'`'}A  : ${customerPhone},${'`'}
  // ${'`'}A  : ${routeBeginCity},${'`'}
  // ${'`'}A  : ${routeEndCity},${'`'}
  // ${'`'}A  : ${routeBeginDate},${'`'}
  // ${'`'}A  : ${customerComment || ' '}${'`'}`;
  const TEXT =
    heading +
    "  <b>I':</b>  " +
    '<u>' +
    customerName +
    '</u>' +
    '  <b> :</b>  ' +
    '<u href="tel:+' +
    customerPhone +
    '">' +
    '+' +
    customerPhone +
    '</u>' +
    '  <b> :</b>  ' +
    '<u>' +
    routeBeginCity +
    '</u>' +
    '  <b> :</b>  ' +
    '<u>' +
    routeEndCity +
    '</u>' +
    '  <b> :</b>  ' +
    '<u>' +
    routeBeginDate +
    '</u>' +
    '  <b> :</b>  ' +
    '<u>' +
    customerComment +
    '</u>';

  fetch(
    `https://api.telegram.org/bot${TOKEN}/sendMessage?chat_id=${driverTelegramID}&parse_mode=HTML&text=${TEXT}`,
    telegramOptions
  )
    .then(response => response.json())
    .then(post => console.log(post))
    .catch(error => console.log(error));
};
